export const imageUploadService = async (fileInput: any) => {

    const myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..eBqduXXjF8T91jCY9l85gTVy-iiV-3cWrhxDtBu_gug");
    myHeaders.append("Content-Type", "application/json");
    console.log(fileInput)
    const formdata = new FormData();
    formdata.append("image", fileInput.files[0], "file");

    const requestOptions: RequestInit = {
    method: 'POST',
    headers: myHeaders,
    body: formdata,
    redirect: "error"
    };

    try {
        const res = await fetch("https://api.test.shuttlers.africa/v1/services/image", requestOptions);
        const result = await res.json();
        return result;
    }
    catch (error) {
        console.log(error)
    }
}